































































































































import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({
  components: {},
})
export default class AboutPage extends Vue {
  @Prop() value!: boolean;

  get show() {
    return this.value;
  }

  set show(value) {
    this.$emit('input', value);
  }

  created() {
    this.$log.debug('--> created()');
  }
}
